<template>
    <div class="step-one text-center">
        <div class="mb-5 badge" style="font-size: 25px;">
            <small>프레임: {{targetFrame ? targetFrame : "선택 안함"}}</small>
        </div>
        <div class="mb-5">
            </div>
            <div class="text-left">
                <div class="mb-3 pl-3"><strong>4 Cuts</strong></div>
                <div class="mb-5 text-center">
                    <div class="row justify-content-center m-0">
                        <div class="col-auto mb-3 p-3" :class="{ 'target': targetFrame == 'b' }">
                        <img class="frame-vertical" src="@/assets/frame/b.jpg" @click="selectFrame('b')" draggable="false">
                        </div>
                        <div class="col-auto mb-3 p-3" :class="{ 'target': targetFrame == 'y' }">
                        <img class="frame-vertical" src="@/assets/frame/y.jpg" @click="selectFrame('y')" draggable="false">
                        </div>
                        <div class="col-auto mb-3 p-3" :class="{ 'target': targetFrame == 't' }">
                        <img class="frame-vertical" src="@/assets/frame/t.jpg" @click="selectFrame('t')" draggable="false">
                        </div>
                    </div>
                </div>

                <hr>
                
            </div>
        </div>
    </div>
</template>

<script>

export default {
    name: 'StepOne',
    data() {
        return {
            targetFrame: null,
        }
    },
    mounted() {
        this.init();
        this.targetFrame = this.$store.getters.getFrame;
        if (!this.targetFrame) this.$store.commit('setNext', false);
        else this.$store.commit('setNext', true);
    },
    beforeDestroy() {
        this.nextStep();
    },
    methods: {
        selectFrame(id) {
            if (this.targetFrame == id) {
                this.targetFrame = null;
                this.$store.commit('setNext', false);
                this.$store.commit('setTable', {
                    'rows': 2,
                    'columns': 2, //1,
                })
                
                console.log(id);
                this.$store.commit('setFrameName', id);
            } else {
                this.targetFrame = id;
                this.$store.commit('setNext', true);
                this.$store.commit('setTable', {
                    'columns': 2, //parseInt(id.split('x')[0]),
                    'rows': 2, //parseInt(id.split('x')[1]),
                })
                console.log(id);
                this.$store.commit('setFrameName', id);
            }
        },
        nextStep() {
            if (!this.targetFrame) return;

            this.$store.commit('setFrame', this.targetFrame);
        },
        init() {
            this.$store.commit('resetState');
        },
    }
}
</script>

<style scoped>
.frame-horizontal {
    margin: auto;
    box-shadow: 0.5px 0.5px 1.5px black;
    width: 300px;
}

.frame-vertical {
    box-shadow: 0.5px 0.5px 1.5px black;
    height: 300px;
}

.target {
    background-color: grey;
}

.badge {
    background-color: grey;
    color: #FFF;
    border-radius: 0.5rem;
}

.step-one {
    padding-bottom: 50px;
}
</style>