<template>
  <div class="step-two">
    <div class="mb-5">
      <!-- 1. 로딩 화면 -->
      <div v-if="isLoading" style="font-size: 20px; height: 600px;">
        <div class="text-center">
          <div><i class="mdi mdi-loading mdi-spin"></i></div>
          <small>카메라 설정 중</small>
        </div>
      </div>

      <!-- 2. 촬영 가능 상태 (가로 모드만) -->
      <div v-else-if="canPhoto">
        <div class="camera-horizontal camera-frame d-flex">
          <!-- 비디오 + 캔버스 + 오버레이 -->
          <div style="position: relative; width: 1080px; height: 1620px;">
            <div class="test-custom" :style="overlayStyle"></div>
            <video
              v-show="!isPhotoTaken"
              ref="camera"
              width="1080"
              height="1620"
              autoplay
            ></video>
            <canvas
              v-show="isPhotoTaken"
              id="photoTaken"
              ref="canvas"
              width="1080"
              height="1620"
            ></canvas>
          </div>

          <!-- 상태 메시지 -->
          <div
            class="col-custom h-100 text-center d-flex align-items-center justify-content-center"
            style="width: 50px; color: #FFF; font-size: 20px;"
          >
            <div v-if="isPhotoTaken">
              마<br>음<br>에<br><br>들<br>면<br><br>다<br>음<br>으<br>로<br>!
            </div>
            <div v-else-if="getImageLen < 4">
              {{ 4 - getImageLen }}<br>장<br><br>남<br>았<br>어<br>요<br>!
            </div>
            <div v-else>
              이<br>제<br><br>꾸<br>미<br>러<br><br>가<br>볼<br>까<br>요<br>?
            </div>
          </div>

          <!-- 촬영/저장 버튼 -->
          <div v-if="!isShotPhoto" class="col-custom" style="width: 50px;">
            <div class="w-100">
              <div
                class="d-flex justify-content-center align-items-center"
                style="height: 75px;"
              >
                <i
                  v-if="isPhotoTaken"
                  class="mdi mdi-trash-can"
                  style="font-size: 30px; color: #FFF;"
                  @click="isPhotoTaken = false"
                ></i>
              </div>
              <div
                class="d-flex justify-content-center align-items-center"
                style="height: 1620px;"
              >
                <div
                  v-if="getImageLen < 4"
                  class="takePic d-flex justify-content-center align-items-center"
                  v-on="isPhotoTaken
                    ? { click: () => { saveImage(); isPhotoTaken = false; } }
                    : { click: () => { takePhoto(); } }"
                >
                  <div class="takePic-inner">
                    <div
                      v-if="isPhotoTaken"
                      class="h-100 d-flex justify-content-center align-items-center"
                    >
                      <i class="mdi mdi-tray-plus" style="font-size: 20px;"></i>
                    </div>
                  </div>
                </div>
                <div
                  v-else
                  class="takePic d-flex justify-content-center align-items-center"
                  style="font-size: 25px;"
                >
                  <i class="mdi mdi-restore" @click="isOpen = true;"></i>
                </div>
              </div>
              <div style="height: 75px;"></div>
            </div>
          </div>
        </div>
      </div>

      <!-- 3. 사진 업로드 모드 -->
      <div v-else class="m-auto" style="height: 1620px; width: 1080px;">
        <div v-if="!getImageLen" class="h-100 card" @click="onUploadClick">
          <div class="m-auto">
            <i class="mdi mdi-arrow-up-bold" style="font-size: 70px;"></i>
            <div><strong>사진 올리기</strong></div>
          </div>
        </div>
        <div v-else class="m-auto h-100">
          <div class="h-100" style="box-shadow: 1px 1px 3px black;">
            <div
              class="m-auto row h-50"
              v-for="(row, rowIdx) of [0, 1]"
              :key="rowIdx"
            >
              <div
                class="col-4 m-auto"
                v-for="(col, colIdx) of [0, 1, 2]"
                :key="colIdx"
              >
                <div
                  v-if="Object.values(images)[rowIdx * 3 + col]"
                  class="card m-auto mb-3"
                  style="position: relative;"
                >
                  <img
                    class="uploadImage"
                    :src="Object.values(images)[rowIdx * 3 + col]"
                  />
                  <div class="overlay">
                    <i
                      class="mdi mdi-close"
                      @click="removeImg(Object.keys(images)[rowIdx * 3 + col])"
                    ></i>
                  </div>
                </div>
                <div v-else class="text-center card" @click="onUploadClick">
                  <i class="mdi mdi-arrow-up-bold" style="font-size: 60px;"></i>
                  <div><strong>사진 올리기</strong></div>
                </div>
              </div>
            </div>
          </div>
          <div v-if="getImageLen === 4" class="text-center">
            <button class="btn-custom" @click="initImage">초기화</button>
          </div>
        </div>
        <div class="text-center" v-if="getImageLen < 4">
          카메라가 없다면 가지고 계신 사진을 6장까지 넣어주세요!
        </div>
        <input
          ref="fileInput"
          @change="onImageUpload"
          type="file"
          multiple
        />
      </div>
    </div>

    <modal
      v-if="isOpen"
      @on-close="isOpen = false"
      @on-submit="isOpen = false; initImage()"
      :title="'사진 삭제'"
      :msg="'찍은 사진들을 모두 초기화 하시겠어요?'"
    />
  </div>
</template>


<script>
import Modal from '../../components/modal.vue'

export default {
    name: 'StepOne',
    components: {
        Modal
    },
    data() {
        return {
            images: {},
            rows: 2,
            columns: 1,
            canPhoto: false,
            isOpen: false,
            isCameraOpen: false,
            isPhotoTaken: false,
            isShotPhoto: false,
            isLoading: false,
            isTarget: null,
            overlayIndex: 1, // ← 오버레이 번호 시작
            overlayImages: [
            require('@/assets/frame/y1.png'),
            require('@/assets/frame/y2.png'),
            require('@/assets/frame/y3.png'),
            require('@/assets/frame/y4.png'),
            require('@/assets/frame/t1.png'),
            require('@/assets/frame/t2.png'),
            require('@/assets/frame/t3.png'),
            require('@/assets/frame/t4.png'),
            ],
            frameName: null,
        }
    },
    created() {
        
    },
    mounted() {
        let table = this.$store.getters.getFrame
        console.log(table)
        this.rows = 2; //table.split('x')[0];
        this.columns = 2; //table.split('x')[1];
        console.log(this.rows, this.columns)
        this.images = this.$store.getters.getImages;

        if (this.getImageLen == 4) this.$store.commit('setNext', true);
        else this.$store.commit('setNext', false);

        const frameName = this.$store.getters.getFrameName;
        if(frameName == 't'){
          this.overlayIndex = 5;
        }else if(frameName == 'y'){
          this.overlayIndex = 1;
        }else{
          this.overlayIndex = 10;
        }
        console.log('넘어온 프레임 파일명dd:', this.overlayIndex);

        this.createCameraElement();       
  window.addEventListener('keydown', this.handleKeyDown);
    },
    beforeDestroy() {
  window.removeEventListener('keydown', this.handleKeyDown);
        // this.showingImage();
        // this.stopCameraStream();
    },
    methods: {
        createCameraElement() {
            this.isLoading = true;
            let camSize = null
            this.rows <= this.columns ? camSize = { width: 1080, height: 1620 } : camSize = { width: 1080, height: 1620 };

            const constraints = (window.constraints = {
                audio: false,
                video: {
                    height: 1620, //camSize.height,
                    width: 1080, //camSize.width,
                },
            });
            

            navigator.mediaDevices.getUserMedia(constraints)
                .then(stream => {
                    this.isLoading = false;
                    this.canPhoto = true;

                    return stream;
                })
                .then(stream => {
                    console.log(stream.getVideoTracks()[0].getSettings())
                    // 비디오 스트림 적용
                    this.$refs.camera.srcObject = stream;

                    // ✅ 비디오에 거울모드 적용
                    this.$refs.camera.style.transform = 'scaleX(-1)';
                    this.$refs.camera.srcObject = stream;
                })
                .catch(error => {
                    console.error(error)
                    this.isLoading = false;
                    this.canPhoto = false;
            });

        },
          handleKeyDown(event) {
    //if (event.code === 'Enter') {
      if (!this.isPhotoTaken) {
        this.takePhoto();
      } else {
        this.saveImage();
        this.isPhotoTaken = false;
      }
    //}
  },

        stopCameraStream() {
            let tracks = this.$refs.camera.srcObject.getTracks();

            tracks.forEach(track => {
                track.stop();
            });
        },

takePhoto() {
  // 1) 실제 보이는 크기 가져오기
  const videoEl = this.$refs.camera;
  const canvasEl = this.$refs.canvas;
  const rect = videoEl.getBoundingClientRect();
  const vw = videoEl.videoWidth;
  const vh = videoEl.videoHeight;
  const cw = rect.width;
  const ch = rect.height;

  // 2) 캔버스 내부 해상도 동기화
  canvasEl.width = cw;
  canvasEl.height = ch;

  // 3) object-fit: cover 와 동일한 크롭 영역 계산
  const scale = Math.max(cw / vw, ch / vh);
  const sw = cw / scale;
  const sh = ch / scale;
  const sx = (vw - sw) / 2;
  const sy = (vh - sh) / 2;

  // 4) 좌우 반전 포함, 캔버스에 그리기
  const ctx = canvasEl.getContext('2d');
  ctx.save();
  ctx.translate(cw, 0);
  ctx.scale(-1, 1);
  ctx.drawImage(
    videoEl,
    sx, sy,      // source x, y
    sw, sh,      // source width, height
    0, 0,        // dest x, y
    cw, ch       // dest width, height
  );
  ctx.restore();

  // 5) 오버레이도 같은 크기로
  const overlayImg = new Image();
  overlayImg.crossOrigin = "anonymous";
  overlayImg.src = this.overlayImages[this.overlayIndex - 1];
  overlayImg.onload = () => {
    ctx.drawImage(overlayImg, 0, 0, cw, ch);
  };
  this.isPhotoTaken = true;
},

        saveImage() {
            const image = this.$refs.canvas.toDataURL("image/jpeg").replace("image/jpeg", "image/octet-stream");
            let id = (new Date).getTime();

            this.$set(this.images, id, image);

            const frameName = this.$store.getters.getFrameName;

            if(frameName == 'y'){
                // 오버레이 번호 증가
                if(this.overlayIndex <= 3){
                  this.overlayIndex++;
                }else{
                  this.overlayIndex = 10;
                }
            }else if(frameName == 't'){              
                if(this.overlayIndex <= 8){
                  this.overlayIndex++;
                }
            }
        },

        showingImage() {
            this.$store.commit('setImages', this.images);
        },
        
        initImage() {
            this.$store.commit('setNext', false);
            this.$store.commit('setImages', {});
            this.$store.commit('setTargets', {});
            this.$store.commit('setImgCanvas', {});
            this.$store.commit('setTmpTargets', {});
            this.$store.commit('setUpdateQueue', []);
            
            this.images = {};
        },

        removeImg(target) {
            this.images[target] = null;
            this.$delete(this.images, target);
            this.$store.commit('setImages', this.images);
        },

        onUploadClick() {
            this.$refs.fileInput.click();
        },

        async onImageUpload(e) {
            if (this.getImageLen >= 4) return;

            let files = Array.from(e.target.files)

            for await (let file of files) {
                let id = (new Date).getTime();

                if (this.getImageLen >= 4) break;
                this.$set(this.images, id, await this.readFile(file));
            }
        },

        readFile(file) {
            return new Promise((resolve, reject) => {
                const reader = new FileReader();
                reader.onload = (e) => {
                    resolve(e.target.result);
                }
                reader.readAsDataURL(file);
            })
        },
    },
    computed: {
        getImageLen() {
            return Object.keys(this.images).length;
        },
        overlayStyle() {
            return {
            backgroundImage: `url(${this.overlayImages[this.overlayIndex - 1]})`,
            backgroundSize: 'cover',
            backgroundPosition: 'top center',
            backgroundRepeat: 'no-repeat'
            };
        },
    },
    watch: {
        images: {
            deep: true,
            handler() {
                if (this.getImageLen == 4) this.$store.commit('setNext', true);
                this.showingImage();
            }
        }
    }
}
</script>

